<template>
  <div class="container">
    <!--  -->
    <van-nav-bar
      fixed
      left-arrow
      @click-left="$router.back()"
      title=""
    ></van-nav-bar>

    <!--  -->
    <div class="chat-list">
      <div
        class="chat-item"
        v-for="(item, idx) in list"
        :key="idx"
        :class="item.name === 'xz' ? 'left' : 'right'"
      >
        <!-- templatedom -->
        <template v-if="item.name === 'xz'">
          <van-image
            fit="cover"
            round
            src="https://img.yzcdn.cn/vant/cat.jpeg"
          />
          <div class="chat-pao">{{ item.msg }}</div>
        </template>
        <template v-else>
          <div class="chat-pao">{{ item.msg }}</div>
          <van-image fit="cover" round :src="$route.query.userInfor" />
        </template>
      </div>
    </div>

    <!--  -->
    <div class="reply-container van-hairline--top">
      <van-field v-model.trim="word" placeholder="...">
        <span @click="send()" slot="button" style="font-size: 12px; color: #999"
          ></span
        >
      </van-field>
    </div>
  </div>
</template>

<script>
import io from 'socket.io-client'
export default {
  name: 'UserChat',
  data () {
    return {
      list: [ // 
        // { name: 'xz', msg: '', timestamp: Date.now() },
        // { name: 'me', msg: ', ', timestamp: Date.now() },
        // { name: 'xz', msg: '', timestamp: Date.now() }
      ],
      word: ''
    }
  },
  created () {
    this.socket = io('http://localhost:8001', {
      query: {
        token: this.$store.state.tokenInfor.token
      },
      transports: ['websocket']
    })

    this.socket.on('connect', () => {
      this.list.push({
        name: 'xz',
        msg: '',
        timestamp: Date.now()
      })
      // console.log('....', this.socket)
    })
    this.socket.on('message', obj => {
      console.log('', obj)
      this.list.push({
        name: 'xz',
        msg: obj.msg,
        timestamp: obj.timestamp
      })
    })
  },
  watch: {
    list () {
      setTimeout(() => {
        document.querySelector('.chat-list').scrollTop = document.querySelector('.chat-list').scrollHeight + 100
      }, 100)
    }
  },
  computed: {
  },
  methods: {
    send () {
      if (this.word === '') {
        return
      }
      // 1. socket.io
      this.socket.emit('message', {
        msg: this.word,
        timestamp: Date.now()
      })
      // 2. 
      this.list.push({
        name: 'me',
        msg: this.word,
        timestamp: Date.now()
      })
      this.word = ''
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
  background: #fafafa;
  padding: 46px 0 50px 0;
  .chat-list {
    height: 100%;
    overflow-y: scroll;
    .chat-item {
      padding: 10px;
      .van-image {
        vertical-align: top;
        width: 40px;
        height: 40px;
      }
      .chat-pao {
        vertical-align: top;
        display: inline-block;
        min-width: 40px;
        max-width: 70%;
        min-height: 40px;
        line-height: 38px;
        border: 0.5px solid #c2d9ea;
        border-radius: 4px;
        position: relative;
        padding: 0 10px;
        background-color: #e0effb;
        word-break: break-all;
        font-size: 14px;
        color: #333;
        &::before {
          content: "";
          width: 10px;
          height: 10px;
          position: absolute;
          top: 12px;
          border-top: 0.5px solid #c2d9ea;
          border-right: 0.5px solid #c2d9ea;
          background: #e0effb;
        }
      }
    }
  }
}
.chat-item.right {
  text-align: right;
  .chat-pao {
    margin-left: 0;
    margin-right: 15px;
    &::before {
      right: -6px;
      transform: rotate(45deg);
    }
  }
}
.chat-item.left {
  text-align: left;
  .chat-pao {
    margin-left: 15px;
    margin-right: 0;
    &::before {
      left: -5px;
      transform: rotate(-135deg);
    }
  }
}
.reply-container {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 44px;
  width: 100%;
  background: #f5f5f5;
  z-index: 9999;
}
</style>
